.connectionState {
    position: absolute;
    inset-inline-start: 10px;
    bottom: 10px;
    padding: 5px;
    box-shadow: 0 0.5px 3px -1px var(--theme-color-windowShadowFg);
    border-radius: 100%;
    transition: bottom 100ms linear;
    background: inherit;
    z-index: 2;

    &.connected {
        bottom: -32px;
    }
}
